import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PricingComponent } from '@app/modules/ai-store/pricing/pricing.component';
import { RouterModule, Routes } from '@angular/router';
import { BasicModule } from '@app/shared/basic-components/basic.module';
import { PricingReslover } from '@app/modules/ai-store/pricing.reslover';
import { CheckOutComponent } from './check-out/check-out.component';
import { NgxSliderModule } from '@angular-slider/ngx-slider';


const routes: Routes = [
  {
    path: '',
    component: PricingComponent,
    resolve:{
      pricing:PricingReslover
    }
  }
]


@NgModule({
  declarations: [PricingComponent, CheckOutComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
    NgxSliderModule
  ],
  providers:[
    PricingReslover
  ]
})
export class AiStoreModule { }
