import { Component, Input, OnInit } from '@angular/core';
import { SaveService } from '@app/core/http/serviceIndex';
import { Subscription } from 'rxjs';
import { ChangeContext, Options } from '@angular-slider/ngx-slider';

@Component({
  selector: 'check-out',
  templateUrl: './check-out.component.html',
  styleUrls: ['./check-out.component.css']
})
export class CheckOutComponent implements OnInit {
  @Input() pricing;

  private sub: Subscription;
  public isLoading: boolean = false;
  public selectedIndex = 0;

  value: number = 0;
  options: Options = {
    showTicksValues: true,
    stepsArray: [
      { value: 0 },
      { value: 1 },
      { value: 3 },
      { value: 6 },
      { value: 9 },
    ],
    getPointerColor: (value: number): string => {

      return '#D6980C';
    }
  };
  constructor(private _service: SaveService) { }

  ngOnInit() {
  }

  onCheckout() {
    this.isLoading = true;
    let data = {
      "stripePriceId": this.pricing.stripePriceId
    }
    this.sub = this._service.onPricing(data)
      .subscribe((res) => {
        window.location.href = res.url;
        this.isLoading = false;
      })
  }

  onCheckoutMock() {
    this.isLoading = true;
    let data = {
      "stripePriceId": this.pricing.items[this.selectedIndex].stripePriceId
    }
    this.sub = this._service.onPricing(data)
      .subscribe((res) => {
        window.location.href = res.url;
        this.isLoading = false;
      })
  }

  onUserChange(changeContext: ChangeContext): void {
    if (this.value == 0) this.selectedIndex = 0;
    else if (this.value == 1) this.selectedIndex = 1;
    else if (this.value == 3) this.selectedIndex = 2;
    else if (this.value == 6) this.selectedIndex = 3;
    else if (this.value == 9) this.selectedIndex = 4;
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if (this.sub) this.sub.unsubscribe();
  }
}
